import { useRef, useEffect } from 'react';

import CodeMirror from 'codemirror'
import 'codemirror/addon/comment/comment'
import 'codemirror/addon/selection/mark-selection'
import 'codemirror/lib/codemirror.css'

import customHostsMode from './customHostsMode'

import './style.css';

customHostsMode()

interface EditorProps {
  readOnly?: boolean;
  onChange?: (value?: string) => void;

}

const Editor: React.FC<EditorProps> = (props) => {
  const {readOnly, onChange} = props

  const ref = useRef<HTMLTextAreaElement>(null)

    useEffect(() => {
    if (!ref.current) return

    let cm = CodeMirror.fromTextArea(ref.current, {
      lineNumbers: true,
      readOnly,
      // 自定义 mode
      mode: 'hosts',
    })

    cm.setSize('100%', '100%')

    cm.on('change', (editor) => {
      const value = editor.getDoc().getValue()
      onChange?.(value)
    })
  }, [])

  return (
    <div className="hosts_editor">
      <textarea className="hosts_editor_textarea" ref={ref}></textarea>
    </div>
  );
}

export default Editor;
